import { useState, useCallback } from "react";
import { Button, Menu, MenuItem, TextField, Icon } from "@mui/material";
import { LocalizationProvider, DatePicker } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { useNavigate } from "react-router-dom";
import styles from "./employe-panel.module.css";

const EmployePanel = () => {
  const [dropdownAnchorEl, setDropdownAnchorEl] = useState(null);
  const [dropdown1AnchorEl, setDropdown1AnchorEl] = useState(null);
  const [dropdown1SelectedIndex, setDropdown1SelectedIndex] = useState(-1);
  const [dropdownDateTimePickerValue, setDropdownDateTimePickerValue] =
    useState(null);
  const navigate = useNavigate();
  const dropdownOpen = Boolean(dropdownAnchorEl);
  const handleDropdownClick = (event) => {
    setDropdownAnchorEl(event.currentTarget);
  };
  const handleDropdownClose = () => {
    setDropdownAnchorEl(null);
  };
  const dropdown1Open = Boolean(dropdown1AnchorEl);
  const handleDropdown1Click = (event) => {
    setDropdown1AnchorEl(event.currentTarget);
  };
  const handleDropdown1MenuItemClick = (index) => {
    setDropdown1SelectedIndex(index);
    setDropdown1AnchorEl(null);
  };
  const handleDropdown1Close = () => {
    setDropdown1AnchorEl(null);
  };

  const onFrameContainerClick = useCallback(() => {
    navigate("/login");
  }, [navigate]);

  const onMenuClick = useCallback(() => {
    navigate("/report");
  }, [navigate]);

  const onFrameButtonClick = useCallback(() => {
    navigate("/overview");
  }, [navigate]);

  const onFrameButton1Click = useCallback(() => {
    navigate("/vehicles");
  }, [navigate]);

  const onFrameButton2Click = useCallback(() => {
    navigate("/charges");
  }, [navigate]);

  const onFrameButton3Click = useCallback(() => {
    navigate("/drivers");
  }, [navigate]);

  const onFrameButton4Click = useCallback(() => {
    navigate("/schedules");
  }, [navigate]);

  const onFrameButton5Click = useCallback(() => {
    navigate("/admin-portal");
  }, [navigate]);

  const onFrameButton6Click = useCallback(() => {
    navigate("/report");
  }, [navigate]);

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <div className={styles.employePanel}>
        <img
          className={styles.employePanelChild}
          alt=""
          src="/rectangle-192@2x.png"
        />
        <img
          className={styles.employePanelChild}
          alt=""
          src="/rectangle-192@2x.png"
        />
        <img
          className={styles.employePanelChild}
          alt=""
          src="/rectangle-192@2x.png"
        />
        <Button
          className={styles.footer}
          disableElevation={true}
          color="primary"
          variant="contained"
          sx={{ borderRadius: "0px 0px 0px 0px" }}
        >
          Logout
        </Button>
        <div className={styles.frameDiv} />
        <div className={styles.bellParent}>
          <img className={styles.bellIcon} alt="" src="/bell@2x.png" />
          <div className={styles.frameParent} onClick={onFrameContainerClick}>
            <div className={styles.frameWrapper}>
              <div className={styles.usernameWrapper}>
                <b className={styles.username}>Username</b>
              </div>
            </div>
            <img
              className={styles.arrowDropDownIcon}
              alt=""
              src="/arrow-drop-down@2x.png"
            />
          </div>
        </div>
        <h2 className={styles.electrifyit}>
          <span>Electrify</span>
          <span className={styles.it}>It</span>
        </h2>
        <img className={styles.image9Icon} alt="" src="/image-9@2x.png" />
        <div className={styles.dropdown}>
          <Button
            sx={{ width: 208 }}
            id="button-Report"
            aria-controls="menu-Report"
            aria-haspopup="true"
            aria-expanded={dropdownOpen ? "true" : undefined}
            onClick={handleDropdownClick}
            color="primary"
          >
            Report
          </Button>
          <Menu
            anchorEl={dropdownAnchorEl}
            open={dropdownOpen}
            onClose={handleDropdownClose}
          >
            <MenuItem onClick={handleDropdownClose}>
              Total Miles Driven
            </MenuItem>
            <MenuItem onClick={handleDropdownClose}>
              Energy Consumption
            </MenuItem>
            <MenuItem onClick={handleDropdownClose}>Cost Analysis</MenuItem>
          </Menu>
        </div>
        <div className={styles.dropdown1}>
          <Button
            sx={{ width: 208 }}
            id="button-Frequency"
            aria-controls="menu-Frequency"
            aria-haspopup="true"
            aria-expanded={dropdown1Open ? "true" : undefined}
            onClick={handleDropdown1Click}
            color="primary"
          >
            Frequency
          </Button>
          <Menu
            anchorEl={dropdown1AnchorEl}
            open={dropdown1Open}
            onClose={handleDropdown1Close}
          >
            <MenuItem
              selected={dropdown1SelectedIndex === 0}
              onClick={() => handleDropdown1MenuItemClick(0)}
            >
              Daily
            </MenuItem>
            <MenuItem
              selected={dropdown1SelectedIndex === 1}
              onClick={() => handleDropdown1MenuItemClick(1)}
            >
              Weekly
            </MenuItem>
            <MenuItem
              selected={dropdown1SelectedIndex === 2}
              onClick={() => handleDropdown1MenuItemClick(2)}
            >
              Monthly
            </MenuItem>
            <MenuItem
              selected={dropdown1SelectedIndex === 3}
              onClick={() => handleDropdown1MenuItemClick(3)}
            >
              Yearly
            </MenuItem>
          </Menu>
        </div>
        <div className={styles.dropdown2}>
          <DatePicker
            label="Time Frame"
            value={dropdownDateTimePickerValue}
            onChange={(newValue) => {
              setDropdownDateTimePickerValue(newValue);
            }}
            sx={{}}
            slotProps={{
              textField: {
                size: "medium",
                fullWidth: false,
                required: false,
                autoFocus: false,
                error: false,
                color: "primary",
                placeholder: "Time Frame",
              },
              openPickerIcon: {
                component: () => <></>,
              },
            }}
          />
        </div>
        <Button
          className={styles.menu}
          disableElevation={true}
          color="primary"
          variant="contained"
          sx={{ borderRadius: "0px 0px 0px 0px" }}
          onClick={onMenuClick}
        >
          Generate Report
        </Button>
        <Button
          className={styles.buttonPrimary}
          color="primary"
          variant="contained"
          sx={{ borderRadius: "0px 0px 0px 0px", width: 343 }}
        >
          Reports
        </Button>
        <div className={styles.employePanelInner1}>
          <div className={styles.frameGroup}>
            <Button
              className={styles.frameChild}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButtonClick}
            >
              Overview
            </Button>
            <Button
              className={styles.frameChild}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton1Click}
            >
              Vehicles
            </Button>
            <Button
              className={styles.frameChild}
              disableElevation={true}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton2Click}
            >
              Chargers
            </Button>
            <Button
              className={styles.frameChild}
              disableElevation={true}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton3Click}
            >
              Drivers
            </Button>
            <Button
              className={styles.frameChild}
              disableElevation={true}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton4Click}
            >
              Schedules
            </Button>
            <Button
              className={styles.frameChild}
              disableElevation={true}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton5Click}
            >
              Admin Portal
            </Button>
            <Button
              className={styles.frameChild}
              disableElevation={true}
              color="primary"
              variant="contained"
              sx={{ borderRadius: "0px 0px 0px 0px" }}
              onClick={onFrameButton6Click}
            >
              Report
            </Button>
          </div>
        </div>
      </div>
    </LocalizationProvider>
  );
};

export default EmployePanel;
